import React, { useEffect } from "react";
import { motion } from "framer-motion"
import anime from "animejs";
import Footer from "../components/Footer";
import PageContent from "../components/PageContent";
import '../css/style.css';
import '../css/Pages.css';

const IsraelPage = () => {
  const headerSVG = "./images/israel_page.svg" 
  const descriptionText = "1948By AI";
  const terrainImage = "./images/israel_terrain.svg";
  const spotData = [
    {
      image: "./images/istockphoto-852178720-1024x1024.webp",
      text: "Old City of Jerusalem  By AI"
    },
    {
      image: "./images/009866453254258.jpg",
      text: "Masada By AI"
    }
  ];
  const iframeHTML = (
    <iframe src="https://www.youtube.com/embed/4tFXPbh3RC4?si=qO4m6s2JNRMixnzg" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share" referrerpolicy="strict-origin-when-cross-origin" allowfullscreen></iframe>
  );
  const betweenChildContent = null;

  useEffect(() => {
    const objectElement = document.querySelector("#svgObject");
    objectElement.addEventListener("load", () => {
      const svgDocument = objectElement.contentDocument;
      const logoElsElements = Array.from(svgDocument.querySelectorAll(".logoEls"));

      console.log("LogoEls Elements:", logoElsElements);
      if (logoElsElements.length > 0) {
        anime({
          targets: logoElsElements,
          keyframes: [
            { translateY: -10, easing: 'easeInOutQuad' },
            { translateY: 0, easing: 'easeOutBounce' },
          ],
          duration: 900,
          loop: true,
          delay: (el, i) => i * 60,
        });
      } else {
        console.error("no .logoEls");
      }
    });
  }, []);

  return (
    <>
      {/* <div></div> */}
      <motion.div
        id="svg-wrapper"
        initial={{ opacity: 0, scale: 0.8 }}
        whileInView={{ opacity: 1, scale: 1 }}
        transition={{ duration: 1 }}
        viewport={{ once: true }}
      >
        <object
          id="svgObject"
          type="image/svg+xml"
          data={headerSVG}
          style={{ width: "100%", height: "auto" }}
        >
          Your browser does not support embedded SVGs.
        </object>
      </motion.div>

      <motion.div
        id="flag"
        initial={{ y: -50, opacity: 0 }}
        whileInView={{ y: 0, opacity: 1 }}
        transition={{ duration: 1 }}
        viewport={{ once: true, amount: 0.4 }}
      >
        
      </motion.div>

      {/* Individual motions for PageContent elements */}
      <motion.div
        initial={{ opacity: 0, y: 50 }}
        whileInView={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.8 }}
        viewport={{ once: true, amount: 0.2 }}
        
      >
        <PageContent
          description={
            <motion.div
              initial={{ y: 50, opacity: 0 }}
              whileInView={{ y: 0, opacity: 1 }}
              transition={{ duration: 1 }}
            >
              {descriptionText}
            </motion.div>
          }
          terrainImg={
            <motion.div
              initial={{ opacity: 0, scale: 0.9 }}
              whileInView={{ opacity: 1, scale: 1 }}
              transition={{ duration: 1 }}
            >
                <img style={{width:"40%"}} src={terrainImage} alt="Terrain" />
            </motion.div>
          }
          spots={spotData.map((spot, index) => ({
            image: (
              <motion.img
                key={`image-${index}`}
                src={spot.image}
                alt={`Spot ${index}`}
                initial={{ y: 50, opacity: 0 }}
                whileInView={{ y: 0, opacity: 1 }}
                transition={{ duration: 1, delay: index * 0.2 }}
              />
            ),
            text: (
              <motion.div
                key={`text-${index}`}
                initial={{ y: 50, opacity: 0 }}
                whileInView={{ y: 0, opacity: 1 }}
                transition={{ duration: 1, delay: index * 0.2 }}
              >
                {spot.text}
              </motion.div>
            )
          }))}
          videoIframe={
            <motion.div
              initial={{ opacity: 0 }}
              whileInView={{ opacity: 1 }}
              transition={{ duration: 1 }}
            >
              {iframeHTML}
            </motion.div>
          }
        />
      </motion.div>

      <Footer />
    </>
  )
};

export default IsraelPage;