import React, { useEffect } from "react"
import anime from "animejs";
import { motion } from "framer-motion"
import Footer from "../components/Footer"
import PageContent from "../components/PageContent"
import '../css/style.css'
import '../css/Pages.css'

const IranPage = () => {
  const headerSVG = "./images/iran_page.svg" 
  const descriptionText = "6NowruzBy AI";
  const terrainImage = "./images/iran_terrain.svg";
  const spotData = [
    {
      image: "./images/depositphotos_310674876-stock-photo-amazing-ruins-of-the-gate.jpg",
      text: "Persepolis 500By AI"
    },
    {
      image: "./images/naghsh-jahan2.jpeg",
      text: "Isfahan By AI"
    },
    {
      image: "./images/Nrodn_muzeum_rnu.jpg",
      text: "National Museum of Iran By AI"
    },
  ];
  const iframeHTML = (
    <iframe
      src="https://www.youtube.com/embed/RPfP-scwUYk?si=b3Jt5Ym61RsZ8Yo2"
      title="YouTube video player"
      frameBorder="0"
      allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
      referrerPolicy="strict-origin-when-cross-origin"
      allowFullScreen
    ></iframe>
  );
const betweenChildContent = '<div>Hi</div>';


  useEffect(() => {
    const objectElements = document.querySelectorAll(".svgObject");
    objectElements.forEach((objectElement) => {
      objectElement.addEventListener("load", () => {
        const svgDocument = objectElement.contentDocument;
        const logoElsElements = Array.from(svgDocument.querySelectorAll(".logoEls")); 
        const terrainElsElements = Array.from(svgDocument.querySelectorAll(".txtEls")); 
        if(terrainElsElements.length > 0){

          const observer = new IntersectionObserver((entries) => {
            entries.forEach((entry) => {
              if (entry.isIntersecting) {
                anime({
                  targets: entry.target, 
                  textContent: [0, 9240],
                  easing: "linear",
                  round: 1,
                  duration: 1000,
                });
                observer.unobserve(entry.target);
              }
            });
          }, {
            threshold: 1, // start anime when window in heigh
          });

          terrainElsElements.forEach((el) => {
            observer.observe(el);
          });

        } else {
          console.error("no .logoEls");
        }
       
        // console.log("txtEls Elements:", logoElsElements);
        if (logoElsElements.length > 0) {
          anime({
            targets: logoElsElements,
            keyframes: [
              { translateY: -10, easing: 'easeInOutQuad' },
              { translateY: 0, easing: 'easeOutBounce' }, 
            ],
            duration: 900,
            loop: true,
            delay: (el, i) => i * 60,
          });

        } else {
          console.error("no .logoEls");
        }
      });
    });
  }, []);

  return (
    <>
    {/* <div></div> */}
    <motion.div
      id="svg-wrapper"
      initial={{ opacity: 0, scale: 0.8 }}
      whileInView={{ opacity: 1, scale: 1 }}
      transition={{ duration: 1 }}
      viewport={{ once: true }}
    >
      <object
        className="svgObject"
        type="image/svg+xml"
        data={headerSVG}
        style={{ width: "100%", height: "auto" }}
      >
        Your browser does not support embedded SVGs.
      </object>
    </motion.div>

    <motion.div
      id="flag"
      initial={{ y: -50, opacity: 0 }}
      whileInView={{ y: 0, opacity: 1 }}
      transition={{ duration: 1 }}
      viewport={{ once: true, amount: 0.4 }}
    >
      
    </motion.div>

    {/* Individual motions for PageContent elements */}
    <motion.div
      initial={{ opacity: 0, y: 50 }}
      whileInView={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.8 }}
      viewport={{ once: true, amount: 0.2 }}
      
    >
      <PageContent
        description={
          <motion.div
            initial={{ y: 50, opacity: 0 }}
            whileInView={{ y: 0, opacity: 1 }}
            transition={{ duration: 1 }}
          >
            {descriptionText}
          </motion.div>
        }
        
        // spare section
        // betweenChild = {<div >-</div>}

        terrainImg={
          <motion.div
            initial={{ opacity: 0, scale: 0.9 }}
            whileInView={{ opacity: 1, scale: 1 }}
            transition={{ duration: 1 }}
          >
              <object
                className="svgObject"
                type="image/svg+xml"
                data={terrainImage}
                style={{ width: "100%", height: "auto" }}
              >
                Your browser does not support embedded SVGs.
              </object>
              {/* <img src={terrainImage} alt="Terrain" /> */}
          </motion.div>
        }
        spots={spotData.map((spot, index) => ({
          image: (
            <motion.img
              key={`image-${index}`}
              src={spot.image}
              alt={`Spot ${index}`}
              initial={{ y: 50, opacity: 0 }}
              whileInView={{ y: 0, opacity: 1 }}
              transition={{ duration: 1, delay: index * 0.2 }}
            />
          ),
          text: (
            <motion.div
              key={`text-${index}`}
              initial={{ y: 50, opacity: 0 }}
              whileInView={{ y: 0, opacity: 1 }}
              transition={{ duration: 1, delay: index * 0.2 }}
            >
              {spot.text}
            </motion.div>
          )
        }))}
        videoIframe={
          <motion.div
            initial={{ opacity: 0 }}
            whileInView={{ opacity: 1 }}
            transition={{ duration: 1 }}
          >
            {iframeHTML}
          </motion.div>
        }
      />
    </motion.div>

    <Footer />
  </>
    )

    
};

export default IranPage;