import React, { useEffect } from "react";
import anime from "animejs";
import { motion } from "framer-motion";
import ArticleInfo from "../components/ArticleInfo";
import Footer from "../components/Footer";
import "../css/style.css";
import "../css/HomePage.css";

const HomePage = () => {
  const articles = [
    {
      title: "",
      date: "2025/04/09",
      description: "6NowruzBy AI",
      content: "",
      image: `${process.env.PUBLIC_URL}/images/hosein-charbaghi-zQ5IehmIGhw-unsplash_1.jpg`,
      link: "/iran",
    },
    {
      title: "",
      date: "2025/04/09",
      description: "1948By AI",
      content: "",
      image: `${process.env.PUBLIC_URL}/images/Screenshot 2025-04-09 at 21.25.16.png`,
      link: "/israel",
    },
    {
      title: "",
      date: "2025/04/09",
      description: "By AI",
      content: "",
      image: `${process.env.PUBLIC_URL}/images/Screenshot 2025-04-09 at 23.52.29.png`,
      link: "/uae",
    },
    {
      title: "",
      date: "2025/04/09",
      description: "1923SufiBy AI",
      content: "",
      image: `${process.env.PUBLIC_URL}/images/Screenshot 2025-03-25 at 09.44.44.png`,
      link: "/turkey",
    },
  ];

  useEffect(() => {
    const objectElement = document.querySelector("#svgObject");
    objectElement.addEventListener("load", () => {
      const svgDocument = objectElement.contentDocument;
      const logoElsElements = Array.from(svgDocument.querySelectorAll(".logoEls"));

      console.log("LogoEls Elements:", logoElsElements);
      if (logoElsElements.length > 0) {
        anime({
          targets: logoElsElements,
          keyframes: [
            { translateY: -10, easing: "easeInOutQuad" },
            { translateY: 0, easing: "easeOutBounce" },
          ],
          duration: 900,
          loop: true,
          delay: (el, i) => i * 60,
        });
      } else {
        console.error("no .logoEls");
      }
    });
  }, []);

  return (
    <>
      {/* SVG logo with motion */}
      <motion.div
        id="svg-wrapper"
        initial={{ opacity: 0, scale: 0.8 }}
        whileInView={{ opacity: 1, scale: 1 }}
        transition={{ duration: 1 }}
        viewport={{ once: true }}
      >
        <object
          id="svgObject"
          type="image/svg+xml"
          data={`${process.env.PUBLIC_URL}/images/logo_txt.svg`}
          style={{ width: "100%", height: "auto" }}
        >
          Your browser does not support embedded SVGs.
        </object>
      </motion.div>

      {/* Introduction with motion */}
      <motion.p
        className="intro"
        initial={{ y: 50, opacity: 0 }}
        whileInView={{ y: 0, opacity: 1 }}
        transition={{ duration: 1 }}
        viewport={{ once: true }}
      >
        
      </motion.p>

      {/* Articles with motion */}
      <motion.div
        initial={{ opacity: 0 }}
        whileInView={{ opacity: 1 }}
        transition={{ duration: 1 }}
        // viewport={{ once: true }}
      >
        {articles.map((article, index) => (
          <motion.div
            key={index}
            initial={{ y: 50, opacity: 0 }}
            whileInView={{ y: 0, opacity: 1 }}
            transition={{ duration: .5, delay: index * 0.3 }}
            // viewport={{ once: true, amount: 0.8}}
          >
            <ArticleInfo
              title={article.title}
              date={article.date}
              description={article.description}
              content={article.content}
              image={article.image}
              link={article.link}
            />
          </motion.div>
        ))}
      </motion.div>

      {/* Footer */}
      <Footer />
    </>
  );
};

export default HomePage;